.made {
  padding-top: 125px;
  padding-bottom: 20px;
  background-color: $sections-background;
  background-repeat: no-repeat;
  background-image: url('../images/about/mobile/milkfon@2x-320.png');
  background-size: contain;
  background-position-y: 90%;
  @media screen and (min-width: 768px) {
    padding-top: 130px;
    padding-bottom: 33px;
    background-image: none;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 125px;
    padding-bottom: 20px;
    background-image: url('../images/about/desktop/milkfon@2x-1200.png');
  }
}
.made .container {
  @media screen and (min-width: 768px) {
    padding-right: 39px;
    padding-left: 36px;
  }
  @media screen and (min-width: 1280px) {
    padding-right: 112px;
    padding-left: 117px;
  }
}
.made-contant {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}
.made-tradition {
  display: block;
  font-family: $headers-font;
  font-size: 18px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;
  color: $label-color;
  margin-bottom: 15px;
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
  }
}
.made-header {
  width: 100%;
  margin: 0 auto 26px;
  text-align: center;
  font-family: $headers-font;
  font-size: 34px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $title-color;
  @media screen and (min-width: 768px) {
    font-size: 48px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.14;
    letter-spacing: 0.06em;
  }
}
.made-card {
  width: 100%;
  @media screen and (min-width: 768px) {
    width: 340px;
  }
  @media screen and (min-width: 1280px) {
    width: 516px;
  }
}
.made-text {
  padding-right: 30px;
  padding-left: 30px;
  text-align: start;
  font-family: $main-font;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.58;
  color: #000000;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.79;
    padding: 0;
    margin-top: 100px;
  }
  @media screen and (min-width: 1280px) {
    margin-top: 106px;
  }
}
.made-title {
  margin-bottom: 35px;
  @media screen and (min-width: 768px) {
    margin-bottom: 37px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.75;
    margin-bottom: 35px;
    letter-spacing: 0.04em;
  }
}
.made-subtitle {
  margin-bottom: 20px;
  color: $simple-text-color;
  @media screen and (min-width: 768px) {
    letter-spacing: 0.04em;
  }
}
.made-indent {
  letter-spacing: 0.04em;
  color: $simple-text-color;
  display: block;
  margin-bottom: 25px;
  @media screen and (min-width: 768px) {
    margin-bottom: 62px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 45px;
  }
}
.made-button {
  padding-right: 38px;
  padding-left: 37px;
  position: relative;
  display: inline-block;
  font-family: $main-font;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.29;
  text-align: start;
  width: 176px;
  height: 44px;
  border: 1px solid transparent;
  border-radius: 22px;
  background-color: #ffffff;
  color: $title-color;
  &:hover,
  &:focus {
    background-color: $secondary-accent-color;
  }
  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.3;
  }
}
.made-arrow {
  position: absolute;
  width: 10px;
  height: 10px;
  right: 37px;
  top: 17px;
  stroke: $title-color;
}
.made-img {
  flex-shrink: 0;
  margin-bottom: 46px;
  @media screen and (min-width: 768px) {
    margin-bottom: 0;
    margin-right: 17px;
    margin-top: 50px;
  }
  @media screen and (min-width: 1280px) {
    margin-right: 74px;
    margin-top: 55px;
  }
}
#more {
  display: none;
}
