.backdrop {
  position: fixed;
  z-index: 111;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.2);

  &.is-hiddenn {
    opacity: 0;
    pointer-events: none;
  }
}
.header-modal {
  display: flex;
}
// body.modal-open {
//   overflow: hidden;
// }
.modal-buy-now {
  width: 95%;
  padding: 0 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: linear-gradient(
      to right,
      rgba(139, 82, 94, 0.562),
      rgba(134, 65, 99, 0.356)
    ),
    url(../images/customer-reviews/ice.jpg);
  // background-image: url();
  // background-color: $mobile-menu-background-color;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 1px rgb(0 0 0 / 14%),
    0 2px 1px rgb(0 0 0 / 20%);
  border-radius: 4px;
  .feedback-box {
    display: none;
  }
  @media screen and (min-width: 480px) {
    width: 450px;
    .feedback-box {
      display: flex;
    }
  }

  @media screen and (min-width: 1280px) {
    width: 528px;
    height: 566px;
  }
}

.button-close {
  width: 20px;
  height: 20px;
  background-color: white;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-left: auto;
  margin-top: 8px;
  margin-right: 8px;

  .icon-close-svg {
    display: block;
    width: 10px;
    height: 10px;
    fill: black;
    transition: 250ms fill cubic-bezier(0.4, 0, 0.2, 1);
  }
}

.button-close {
  .icon-close-svg {
    &:hover {
      stroke: red;
    }
  }
}

.modal-title {
  width: 92%;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.5;
  text-align: center;
  color: black;
}

.bio-list {
  display: flex;
  flex-wrap: wrap;

  font-weight: 600;
  font-size: 13px;
  line-height: 1.5;
  color: black;
}

.bio-item {
  position: relative;
  &:not(:last-child) {
    margin-bottom: 8px;
  }
}

.modal-buy-now input {
  background-color: #e1e1e1;
  border-radius: 4px;
  border: 1px solid red;
}

.options-list {
  font-weight: 600;
  font-size: 13px;
  line-height: 1.5;
  color: black;
}

.sides-list {
  display: flex;

  font-weight: 600;
  font-size: 13px;
  line-height: 1.5;
  color: black;
}

.sides-item {
  &:not(:last-child) {
    margin-right: 18px;
  }
}

.delivery-list {
  font-weight: 600;
  font-size: 13px;
  line-height: 1.5;
  color: black;
}

.feedback-box {
  display: flex;
  flex-direction: column;

  font-weight: 600;
  font-size: 13px;
  line-height: 1.5;
  color: black;
}

.feedback-box label {
  margin-left: 10px;
}

.feedback-box textarea {
  background-color: #e1e1e1;
  border-radius: 4px;
  border: 1px solid red;
}

.options-subtitle {
  font-weight: 600;
  font-size: 14px;
  line-height: 1.5;
  color: black;
}

.feedback-box-checkbox {
  font-weight: 600;
  font-size: 10px;
  line-height: 1.5;
  color: black;

  margin-bottom: 10px;
}

.button-submit {
  width: 150px;
  height: 30px;
  background: #d41443;
  box-shadow: 0px 4px 4px rgb(0 0 0 / 15%);
  border-radius: 4px;
  color: white;
  border: none;
  text-align: center;
  margin: 0 auto 10px;
  display: block;
  cursor: pointer;
}
