@media (max-width: 768px) {
  .footer {
    padding-top: 56px;
  }
  .footer-container {
    margin-bottom: 43px;
  }
  .footer-text {
    margin-bottom: 43px;
  }
  .footer-label {
    width: 82.857142857142%;
    padding-left: 0.714285714285%;
    margin-bottom: 17px;
  }
  .footer-title {
    width: 71.4285714285%;
  }
  .footer-copyright {
    padding: 22px 29% 25px 25%;
  }
}

@media (min-width: 768px) {
  .footer {
    padding-top: 82px;
  }
  .footer-container {
    display: flex;
    justify-content: space-between;
    padding: 0 92px 0 36px;
    margin-bottom: 91px;
  }
}

@media (min-width: 768px) and (max-width: 1280px) {
  .footer-label {
    padding-top: 7px;
    margin-bottom: 16px;
  }
  .footer-title,
  .footer-label {
    width: 340px;
  }
  .footer-copyright {
    padding: 29px 0 18px 0;
  }
}

@media (min-width: 1280px) {
  .footer-container {
    padding: 0 116px 0 114px;
    margin-bottom: 97px;
  }
  .footer-label {
    width: 472px;
    margin-bottom: 15px;
  }
  .footer-title {
    width: 474px;
  }
  .footer-links {
    margin-bottom: 1px;
  }
  .footer-social {
    padding-right: 5px;
    margin-bottom: 50px;
  }
  .footer-tel {
    padding-bottom: 1px;
  }
  .footer-copyright {
    padding: 9px 0 10px 0;
  }
}

.footer-social {
  display: flex;
  justify-content: flex-end;
  @media screen and(max-width: 300px) {
    gap: 10.0857142857142%;
  }
  @media screen and(max-width: 1280px) {
    margin-bottom: 46px;
  }
  @media screen and(min-width: 300px) and(max-width: 1280px) {
    gap: 30px;
  }
}

.footer-link {
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  &:nth-child(1) {
    & > .footer-icon {
      width: 58.8125%;
    }
    @media screen and(min-width:1280px) {
      margin-right: 24px;
    }
  }
  &:nth-child(2) {
    & > .footer-icon {
      width: 60%;
    }
    @media screen and(min-width:1280px) {
      margin-right: 25px;
    }
  }
  &:nth-child(3) {
    & > .footer-icon {
      height: 60%;
    }
  }
}

.footer-icon {
  max-height: 100%;
  max-width: 100%;
  stroke: none;
}
